export const IS_BROWSER = typeof window !== `undefined`;

export function getUrlHash() {
  if (IS_BROWSER) {
    return decodeURI(window.location.hash).replace('#', '');
  }
  return null;
}

export function getHistoryAPI() {
  if (IS_BROWSER) {
    return window.history;
  }
  return {
    pushState() {},
  };
}
